export function askDeviceMotion(funcArg){
  if (DeviceMotionEvent && typeof DeviceMotionEvent.requestPermission === 'function') {
    DeviceMotionEvent.requestPermission()
    .then(response => {
      if (response === 'granted') {
        this.grantedDeviceMotion(funcArg);
      }
    })
    .catch(console.error)
  }else{
    this.grantedDeviceMotion(funcArg);
  }
}

export function askDeviceOrientation(funcArg){
  if (DeviceOrientationEvent && typeof DeviceOrientationEvent.requestPermission === 'function') {
  DeviceOrientationEvent.requestPermission()
    .then(response => {
      if (response === 'granted') {
        alert('granted device permission');
        grantedDeviceOrientation(funcArg);
      } else {
        alert('not granted device permission');
        // non iOS 13+
      }
    })
    .catch(console.error)
  }else {
    alert('attempting device permission');
    grantedDeviceOrientation(funcArg);
  }
}

export function grantedDeviceMotion(funcArg){
  window.addEventListener('devicemotion', (e) => {
    funcArg(e)
    // do something with e
  })
}

export function grantedDeviceOrientation(funcArg){
  window.addEventListener('deviceorientation', (e) => {
    funcArg.deviceOrientation(e);
    // do something with e
  });
  window.addEventListener('orientationchange', (e) => {
    funcArg.orientationChange(e);
    // do something with e
  });
};
